import React from "react";
import CalculateIcon from "@mui/icons-material/Calculate";
import { StyledText } from "./InfoBoxAccounting.styled";

export const InfoBoxAccounting = () => {
  return (
    <>
      <CalculateIcon fontSize="large" />
      <StyledText>
        <h2>Regnskap</h2>
        <p>
          Vi srger for bokfring og et oppdatert regnskap, som er et viktig
          verkty og som vil gi innsikt i bedriftens konomiske situasjon og som
          hjelper med  ta de riktige beslutningene. Du bestemmer hvor involvert
          du vil vre med i prosessen. Vi kan samarbeide p forskjellige mter.
          Vi kan utfre alt, dere kan utfre deler av oppgavene eller vi kun
          skal kontrollere. Vi srger for at bedriftens behov blir ivaretatt. Vi
          tilpasser bde rutiner, tjenester og systemer til bedriftens behov.
        </p>
      </StyledText>
    </>
  );
};
