import React from "react";
import { StyledContainer } from "./Personvern.styled";

export const Personvern = () => {
  return (
    <StyledContainer>
      <h1>Personvernserklring</h1>
      <section>
        <h2>Hvilke personopplysninger vi samler inn</h2>
        <p>
          Vi samler kun inn anonym informasjon for statistikk og bruksmnstre:
          <br />
           Antall beskende p nettsiden
          <br />
           Klikk p telefonnummer, e-postlenke og lenke til sosiale medier
          <br />
          Dette gjres for  forst hvordan nettsiden brukes og forbedre
          brukeropplevelsen. Ingen personlig identifiserbar informasjon lagres.
        </p>
      </section>
      <section>
        <h2>Verkty vi bruker</h2>
        <p>
           <strong>Google Analytics 4</strong> Brukes til  telle besk og
          spore klikk p telefonnummer, e-post og sosiale medier-lenke.
          Informasjonen er aggregert og kan ikke brukes til  identifisere
          enkeltpersoner <br />
          Google Analytics bruker informasjonskapsler (cookies). Disse aktiveres
          frst etter at du har gitt samtykke via vrt
          informasjonskapsel-banner. Du kan lese mer om hvordan Google hndterer
          data her: &nbsp;
          <a href=" https://policies.google.com/privacy">
            https://policies.google.com/privacy
          </a>
        </p>
      </section>
      <section>
        <h2>Informasjonskapsler (cookies)</h2>
        <p>
          Nettstedet bruker informasjonskapsler for anonym statistikk. Du kan
          velge  godta eller avsl dette i informasjonskapsel-banneret som
          vises ved frste besk.
        </p>
        <section>
          <h3>Hva er informasjonskapsler?</h3>
          <p>
            Informasjonskapsler er sm tekstfiler som lagres i nettleseren din
            nr du besker et nettsted. De brukes blant annet til  huske
            innstillinger, forbedre ytelse og samle anonym statistikk om bruk av
            nettstedet.
          </p>
        </section>
        <section>
          <h3>Hvilke informasjonskapsler bruker vi?</h3>
          <p>Vi bruker flgende typer informasjonskapsler:</p>
          <h4>1. Ndvendige informasjonskapsler</h4>
          <p>
            Disse er ndvendige for at nettstedet skal fungere som det skal og
            kan ikke deaktiveres i systemene vre. De settes vanligvis bare som
            svar p handlinger du gjr, for eksempel nr du godtar
            cookie-banneret.
          </p>
          <h4>2. Analyse (valgfritt)</h4>
          <p>
            Vi bruker Google Analytics for  samle anonym statistikk om hvordan
            nettstedet brukes  for eksempel hvilke sider som er mest beskt, og
            hvor lenge beskende blir p siden. Dette hjelper oss  forbedre
            innholdet vrt. Disse informasjonskapslene lagres bare etter at du
            har gitt samtykke via vrt cookie-banner.
          </p>
        </section>
      </section>
    </StyledContainer>
  );
};
